@import 'ngx-toastr/toastr';
@import "./app/themes/themes.css";

html, body {
  height: 100%;
  margin: 0;
  font-family: var(--mat-sys-plain-font-family);
}

body {
  background-color: var(--mat-sys-background);
  color: var(--mat-sys-on-background);

  .mat-toolbar {
    background-color: var(--mat-sys-primary);
    color: var(--mat-sys-on-primary);
    box-shadow: var(--mat-sys-level2);

    mat-icon {
      color: var(--mat-sys-on-primary); // Ensure icons in toolbar remain white for contrast
    }

    mat-button-toggle-group {
      height: 100%;
      .mat-button-toggle {
        color: var(--mat-sys-on-primary); // Text color for toggle options
        background-color: transparent;
        border: 1px solid color-mix(in srgb, var(--mat-sys-on-primary), transparent 60%);
        border-radius: 4px;
        padding: 2px 8px;
        margin: 0 2px;

        mat-icon {
          color: var(--mat-sys-on-primary); // Icon color in toggle
        }

        &:hover {
          background-color: color-mix(in srgb, var(--mat-sys-on-primary), transparent 90%);
        }

        &.mat-button-toggle-checked {
          background-color: var(--mat-sys-primary);
          color: var(--mat-sys-on-primary);
          border-color: var(--mat-sys-on-primary);

          mat-icon {
            color: var(--mat-sys-on-primary);
          }
        }
      }
    }
  }

  .mat-step-header {
    .mat-step-icon,
    .mat-step-icon-selected,
    .mat-step-icon-state-done,
    .mat-step-icon-state-edit {
      background: none !important;
    }
    .mat-step-label {
      color: #ffffff;
    }
  }
  .dark-theme .mat-step-header .mat-step-label {
    color: #ffffff;
  }

  .dark-theme {
    a {
      letter-spacing: var(--mat-sys-headline-small-tracking);
      color: white;
    }
  }

  a {
    letter-spacing: var(--mat-sys-headline-small-tracking);
    color: rgb(0, 0, 0);
  }

  h1 {
    font: var(--mat-sys-headline-large);
    letter-spacing: var(--mat-sys-headline-large-tracking);
    color: var(--mat-sys-on-background);
  }

  a {
    letter-spacing: var(--mat-sys-headline-small-tracking);
    color: white;
  }

  h3 {
    font: var(--mat-sys-headline-medium);
    letter-spacing: var(--mat-sys-headline-medium-tracking);
    color: var(--mat-sys-on-background);
  }

  h5 {
    font: var(--mat-sys-headline-small);
    letter-spacing: var(--mat-sys-headline-small-tracking);
    color: var(--mat-sys-on-surface);
  }

  label {
    font: var(--mat-sys-label-large);
    letter-spacing: var(--mat-sys-label-large-tracking);
    color: var(--mat-sys-on-background);
  }

  select {
    background-color: var(--mat-sys-surface-container);
    color: var(--mat-sys-on-surface);
    font: var(--mat-sys-body-medium);
    letter-spacing: var(--mat-sys-body-medium-tracking);
  }

  .mat-card {
    background-color: var(--mat-sys-surface-container);
    color: var(--mat-sys-on-surface);
    box-shadow: var(--mat-sys-level1);
  }

  .mat-divider {
    border-top-color: var(--mat-sys-outline);
  }

  .mat-list-item {
    &:hover {
      background-color: color-mix(in srgb, var(--mat-sys-primary), transparent 92%);
    }
    &.active {
      background-color: color-mix(in srgb, var(--mat-sys-primary), transparent 88%);
    }
  }

  .mat-menu-panel {
    background-color: var(--mat-sys-surface-container-high);
    color: var(--mat-sys-on-surface);

    .mat-menu-item {
      font: var(--mat-sys-body-medium);
      letter-spacing: var(--mat-sys-body-medium-tracking);
      &:hover {
        background-color: color-mix(in srgb, var(--mat-sys-primary), transparent 92%);
      }
    }

    &.dark-mode-menu {
      .mat-menu-item {
        display: flex;
        align-items: center;
        gap: 8px;

        mat-icon {
          margin-right: 8px;
          color: var(--mat-sys-on-surface);
        }

        span {
          font: var(--mat-sys-body-medium);
          letter-spacing: var(--mat-sys-body-medium-tracking);
        }

        &.selected-theme {
          background-color: color-mix(in srgb, var(--mat-sys-primary), transparent 88%);
        }
      }
    }
  }

  .color-theme-menu {
    .theme-menu-item {
      display: flex;
      align-items: center;
      gap: 8px;

      .color-preview {
        width: 20px;
        height: 20px;
        border-radius: var(--mat-sys-corner-small);
        border: 1px solid var(--mat-sys-outline);
      }

      span {
        font: var(--mat-sys-body-medium);
        letter-spacing: var(--mat-sys-body-medium-tracking);
      }
    }
  }

  .mat-sidenav-content {
    background-color: var(--mat-sys-background);
    color: var(--mat-sys-on-background);
  }

  mat-icon {
    color: var(--mat-sys-on-surface);
  }

  footer {
    background-color: var(--mat-sys-surface-container);
    color: var(--mat-sys-on-surface);

    a {
      color: var(--mat-sys-on-surface);
      &:hover {
        color: var(--mat-sys-primary);
      }
    }
  }

  input {
    background-color: var(--mat-sys-surface-container);
    border-color: var(--mat-sys-outline);
    color: var(--mat-sys-on-surface);
    font: var(--mat-sys-body-medium);
    letter-spacing: var(--mat-sys-body-medium-tracking);
    &::placeholder {
      color: color-mix(in srgb, var(--mat-sys-on-surface), transparent 60%);
    }
    &:focus {
      border-color: var(--mat-sys-primary);
      box-shadow: 0 0 0 3px color-mix(in srgb, var(--mat-sys-primary), transparent 90%);
    }
    &[readonly] {
      background-color: var(--mat-sys-surface-container-high);
    }
    &.border-gray-300 {
      border-color: var(--mat-sys-outline);
    }
    & + svg.text-gray-400 {
      stroke: var(--mat-sys-on-surface);
      color: var(--mat-sys-on-surface);
    }
  }

  button {
    &.bg-blue-600 {
      background-color: var(--mat-sys-primary);
      color: var(--mat-sys-on-primary);
      &:hover {
        background-color: color-mix(in srgb, var(--mat-sys-primary), transparent 20%);
      }
    }
    &.bg-gray-200 {
      background-color: var(--mat-sys-surface-container-high);
      color: var(--mat-sys-on-surface);
      &:hover {
        background-color: var(--mat-sys-surface-container);
      }
    }
    &.bg-red-600 {
      background-color: var(--mat-sys-error);
      color: var(--mat-sys-on-error);
      &:hover {
        background-color: color-mix(in srgb, var(--mat-sys-error), transparent 20%);
      }
    }
    &:focus {
      box-shadow: 0 0 0 3px color-mix(in srgb, var(--mat-sys-primary), transparent 80%);
    }
  }

  table {
    thead {
      background-color: var(--mat-sys-surface-container-high);
    }
    tbody {
      tr {
        background-color: var(--mat-sys-surface-container);
      }
    }
  }

  .divide-gray-200 {
    border-color: var(--mat-sys-outline);
  }

  .bg-gray-50 {
    background-color: var(--mat-sys-surface-container-high);
  }

  .bg-white {
    background-color: var(--mat-sys-surface);
  }

  .text-gray-500,
  .text-gray-900 {
    color: var(--mat-sys-on-surface);
  }

  svg, .text-gray-500, .text-gray-900 {
    stroke: var(--mat-sys-on-surface);
    color: var(--mat-sys-on-surface);
  }

  .shadow-lg {
    box-shadow: var(--mat-sys-level3);
  }

  .dark-mode-toggle {
    &:hover {
      background-color: color-mix(in srgb, var(--mat-sys-primary), transparent 90%);
    }
  }
}

.mat-sidenav-container {
  height: calc(100vh - 64px);
}
